.phonebook {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  gap: 20px;
}
.form {
  position: fixed;
  top: 20%;
  width: 90%;
  background: var(--bg-color);
  flex-direction: column;
  padding: 20px;
  gap: 30px;
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 500px -9px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 0px 0px 500px -9px rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 500px -9px rgba(0, 0, 0, 0.4);

  &.dark-form {
    -webkit-box-shadow: 0px 0px 500px -9px rgb(255, 255, 255, 0.6);
    -moz-box-shadow: 0px 0px 500px -9px rgba(255, 255, 255, 0.6);
    box-shadow: 0px 0px 500px -9px rgba(255, 255, 255, 0.6);
  }
}
.form label {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.form span {
  font-size: 25px;
  font-weight: 600;
}
.form input {
  width: 100%;
  height: 50px;
  border-radius: 10px;
  font-size: large;
  padding: 5px;
  border: none;
  background: var(--main-color);
  -webkit-box-shadow: 0px 0px 25px -9px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 0px 0px 25px -9px rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 25px -9px rgba(0, 0, 0, 0.4);
}

.form button {
  padding: 10px 20px;
  background: greenyellow;
  border: none;
  border-radius: 10px;
  font-size: 20px;
  width: 100%;
  cursor: pointer;
}

.add {
  border-radius: 50%;
  background: var(--main-color);
  font-size: 70px !important;
  padding: 10px;
  -webkit-box-shadow: 0px 0px 25px -9px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 0px 0px 25px -9px rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 25px -9px rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.displayInfo {
  position: fixed;
  top: 20%;
  background: var(--main-color);
  -webkit-box-shadow: 0px 0px 500px -9px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 0px 0px 500px -9px rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 500px -9px rgba(0, 0, 0, 0.4);
  width: 90%;
  flex-direction: column;
  gap: 30px;
  border-radius: 10px;
  padding: 20px;
  cursor: pointer;

  &.displayInfo-dark {
    -webkit-box-shadow: 0px 0px 500px -9px rgb(255, 255, 255, 0.6);
    -moz-box-shadow: 0px 0px 500px -9px rgba(255, 255, 255, 0.6);
    box-shadow: 0px 0px 500px -9px rgba(255, 255, 255, 0.6);
  }
}
.displayInfo img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 50%;
}

.displayInfo span {
  font-size: large;
}

.buttons {
  width: 100%;
  justify-content: space-around;
}

.buttons button {
  width: 120px;
  height: 40px;
  background: blue;
  border: none;
  border-radius: 5px;
  color: var(--main-color);
  font-size: large;
  cursor: pointer;
}

.delete {
  background: red !important;
}

.span {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
